.navbar {
  width: 100%;
  height: 80px;
  position: fixed;
  display: flex;

  align-items: center;
  justify-content: center;
  top: 0;
  background-color: #fffffa;
  z-index: 1;
  -ms-overflow-style: none;
  -webkit-scrollbar {
    width: 5px;
  }
}
.wrapper {
  width: 100%;
  max-width: 1300px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 20px;
}
.links-navigate {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}
.links-navigate > ul {
  list-style: none;
  text-decoration: none;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 20px;
}
.links-navigate > ul > :link {
  text-decoration: none;
  color: #000;
  font-size: 20px;
  font-weight: 500;
  transition: all 0.3s ease;
}
.Icons {
  width: 300px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

/* responsive screen */
@media screen and (max-width: 768px) {
  .navbar {
    position: fixed !important ;
    top: 0;
    left: 0;
    width: 100%;
    height: 80px;
    height: 60px;
  }
  .links-navigate > ul {
    position: absolute;
    top: 120px;
    left: 0;
    width: 100%;
    height: 0px;
    font-size: 16px;
    background-color: aquamarine;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 10px;
  }
  .Icons {
    width: 200px;
  }
}
